import order from '../../../mock/order.js';
import noData from '../../../components/lists/nullData'
import { toPayment , confirmOrder} from "./orderFun";
import uniLoadMore from '@dcloudio/uni-ui/lib/uni-load-more/uni-load-more.vue';
const TRADESTATUS = [
  {title: '全部', status: ''},
  {title: '待付款', status: 'WAIT_BUYER_PAY'},
  {title: '待发货', status: 'WAIT_SELLER_SEND_GOODS'},
  {title: '待收货', status: 'WAIT_BUYER_CONFIRM_GOODS'},
  {title: '待评价', status: 'TRADE_FINISHED'},
  {title: '已取消', status: 'CANCEL'},
];
const columns = [
  '不想买了', '重复下单', '订单有误', '其它原因',
];
export default {
  components:{
    uniLoadMore,
    noData
  },
  data() {
    return {
      cancelRadio:'',
      tradeStatus: TRADESTATUS,
      columns,
      active:0,
      orderLists: order.orderLists,
      currentPage:1,
      lists:[],
      loadStatus: 'more',
      pullUpOn : true,
      loadding:false,
      orderLoading:true,
      cancel:{
        tid: '',
        cancel_reason: '',
      },
      showPopup:false
    };
  },
  onLoad:function(option){
    if(option.active){
      this.active = option.active
    }
  },
  onShow(){
    this.getLists(true)
  },
  methods: {
    toEvaluate(tid){
      this.toQueryLink('/pages/user/order/evaluate',{
        tid:tid
      })
    },
    receiptOrder(id){
      confirmOrder(id,this.callBack)
    },
    callBack(){
      this.getLists(true)
    },
    paymentSubmit(id){
     toPayment(id)
    },
    submitCancel(){
      if(this.cancelRadio === ''){
        this.showAlert('请选择取消原因')
        return
      }
      this.$http({
        aurl:'cancelOrder',
        data:this.cancel,
        success:res => {
          if(res.data.code===0){
            this.getLists(true)
            this.showPopup = false
          }
        },
        fail:error => {
          console.log(error)
        }
      })
    },
    cancelTid(tid){
      this.cancel.tid = tid
      this.showPopup = true
    },
    closeModel(){
      this.cancelRadio = ''
      this.showPopup = false
    },
    cancelChange(index){
      this.cancelRadio = String(index.detail)
      this.cancel.cancel_reason = this.columns[index.detail]
    },
    cancelClick(index){
      this.cancelRadio = String(index)
      this.cancel.cancel_reason = this.columns[index]
    },
    changeStatus(e) {
      this.active = e.detail.index
      this.getLists(true)
    },
    init(){
      this.loadding = false;
      this.orderLoading = true
      this.loadStatus = 'more';
      this.pullUpOn = true;
      this.currentPage = 1;
      this.lists = []
    },
    getLists(init){
      if(init){
        this.init()
      }
      if (!this.pullUpOn) return;
      if(this.currentPage===1){
        uni.showLoading({
          title: '加载中'
        });
      }
      const _this = this
      const status = this.tradeStatus[this.active].status;
      const params = {
        page:this.currentPage,
        status:status
      }
      this.$http({
        aurl:'orderLists',
        params:params,
        success:res => {
          if(res.data.result.data.length===0){
            _this.loadding = false;
            _this.pullUpOn = false;
            _this.loadStatus = 'noMore'
          }else{
            res.data.result.data.forEach((item) => {
              for(let i = 0 ;i<item.trade_order.length;i++){
                  const v = item.trade_order[i]
                  if(v.after_sales_status){
                    item.sale = true;
                    if(v.after_sales_status==='SELLER_REFUSE_BUYER'){
                      item.sale = false;
                      break;
                    }
                  }
              }
              _this.lists.push(item)
            })
            if(res.data.result.last_page===1){
              _this.loadding = false;
              _this.pullUpOn = false;
              _this.loadStatus = 'noMore'
            }else{
              _this.currentPage++;
              _this.loadStatus = 'more';
              _this.loadding = false;
            }
          }
          setTimeout(function(){
            _this.orderLoading = false
            uni.hideLoading()
          },300)
        },
        fail:error => {
          console.log(error)
        }
      })
    }
  },
  onReachBottom(){
   this.getLists()
  },
  mounted() {
  },
};
